.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding-block: 32px 16px;
  padding-inline: 16px;
  display: flex;
}

.card__container {
  margin-inline: auto;
  flex: 0 134px;
  max-width: 160px;
  margin-bottom: 40px;
}

.card__image {
  width: 100%;
  height: 100%;
  display: block;
}

.card__title {
  color: #060b35;
  text-align: start;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__subtitle {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__activity, .card__price {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.card__activity {
  margin-bottom: 24px;
}

.card__review {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
}

.card__price {
  margin-bottom: 16px;
}

.card__sum-title {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__sum {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.page {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, Inria Serif, serif;
}

.button {
  box-sizing: border-box;
  color: #fff;
  text-align: center;
  cursor: pointer;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  outline: none;
  max-width: 166px;
  height: 40px;
  padding: 12px 69.5px;
  font-size: 14px;
  font-weight: 700;
  display: block;
}

.button--hover:hover {
  color: #00acdc;
  background-color: #fff;
}

.rating {
  flex-direction: row;
  display: flex;
}

.rating__star {
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.rating__star:not(:last-child) {
  margin-right: 4px;
}

.rating__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.rating__star:nth-child(5) {
  background-image: url("star.822d561e.svg");
}
